@include tablet{
    .contenedor-anuncios{
        @include grid(3, 2rem);
    }
}

.anuncio{
    background: lighten($gris, 5);
    border: 1px solid darken($gris, 10);
    margin-top: $separacion/2;

    .contenido-anuncio{
        padding: 2rem;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    h3, p{
        margin: 0;
    }
}
.precio{
    color: $verde;
    font-weight: $bold;
    font-size: 2.4rem;
}

.iconos-caracteristicas{
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;

    li{
        flex: 1;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        img{
            flex: 0 0 5rem;
        }

        p{
            font-weight: $black;
        }
    }
}