.contenedor-carga{
    background-color: rgba(0, 0, 0, 0.836);
    height: 100%;
    width: 100%;
    position: fixed;
    transition: all 1s ease;
    z-index: 10000;
}

.carga{
    border: 1.5rem solid #ccc;
    border-top-color: $naranja;
    border-top-style: groove;
    height: 10rem;
    width: 10rem;
    border-radius: 100%;

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;

    animation: girar 1.5s linear infinite;
}

@keyframes girar {
    from {transform: rotate(0deg);}
    to {transform: rotate(360deg);}
}